$base-font-family: 'Open Sans', sans-serif;
$base-font-size: 18px;
$base-line-height: 1.45;
$base-font-wight: 400;

$base-bg-color: var(--wp--preset--color--base-bg);
$base-color: var(--wp--preset--color--base);
$base-border-color: var(--wp--preset--color--border);

$primary-color: var(--wp--preset--color--primary);
$secondary-color: var(--wp--preset--color--secondary);

$headline-color: var(--wp--preset--color--headline);

$nav-bg-color: var(--wp--preset--color--nav-bg);
$nav-color: var(--wp--preset--color--nav);
$nav-active-color: var(--wp--preset--color--nav-act);

$subnav-bg-color: var(--wp--preset--color--subnav-bg);
$subnav-color: var(--wp--preset--color--subnav);
$subnav-active-color: var(--wp--preset--color--subnav-act);

$widgets-bg-color: var(--wp--preset--color--widgets-bg);
$widgets-color: var(--wp--preset--color--widgets);

$footer-bg-color: var(--wp--preset--color--footer-bg);
$footer-color: var(--wp--preset--color--footer);

/*
$danger: #ff4444;
