$xxs: 420px;
$xs: 600px;
$sm: 768px;
$md: 992px;
$lg: 1200px;
$xl: 1400px;

$breakpoint-nav: 1200px;

$content-max-width: 1400px;
$container-padding-mobile: 20px;
$container-padding-desktop: 50px;
$container-width-mobile: (2 * $container-padding-mobile + $content-max-width);
$container-width-desktop: (2 * $container-padding-desktop + $content-max-width);


$nav-height-mobile: 60px;
$nav-height-desktop: 100px;

$font-path: "assets/fonts" !default;

