@font-face {
    font-family: 'Open Sans';
    src: url('#{$font-path}/OpenSans/OpenSans-Bold.woff2') format('woff2'),
    url('#{$font-path}/subset-OpenSans-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Open Sans';
    src: url('#{$font-path}/OpenSans/OpenSans-Regular.woff2') format('woff2'),
    url('#{$font-path}/subset-OpenSans-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}