*,
*:before,
*:after {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    line-height: $base-line-height;
    scroll-behavior: smooth;
    font-size: $base-font-size;
}

body {
    margin: 0;
    font-family: $base-font-family;
    background-color: $base-bg-color;
    color: $base-color;
    font-weight: $base-font-wight;
    padding-top: $nav-height-mobile;

    @media (min-width: $breakpoint-nav) {
        padding-top: $nav-height-desktop;
    }
}

#content {
    @media (min-width: $lg) {
        font-size: 1.333em;
    }
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: bottom;

    &[src$=".svg"] {
        width: 100%;
    }
}

figure {
    margin: 0;
}

h1, h2, h3, h4, h5, .h3 {
    font-weight: normal;
    color: $headline-color;
}

h1 {
    font-size: 2em;
    margin: .67em 0;
    line-height: 1;
}

h2:not(.post-title) {
    font-size: 1.5em;
    margin: 1.1em 0 .52em;
    line-height: 1.2;
    position: relative;
    padding-bottom: .3em;
}

.h3,
h3 {
    font-size: 1.35em;
    margin: .95em 0 .48em;
    line-height: 1.2;
}

h4:not(.post-title) {
    font-size: 1.2em;
    margin: .95em 0 .48em;
    line-height: 1.2;
}

h5 {
    font-size: 1.1em;
    margin: .95em 0 .48em;
    line-height: 1.2;
}


b, strong {
    font-weight: 700;
}

a {
    color: $primary-color;
    transition: color 250ms linear;

    &:hover {
        color: $base-color;
    }

    &:active {
        background-color: rgba($primary-color, .3);
    }

    &.more-link {
        display: inline-block;
    }
}

ul.is-style-highlights {
    list-style-type: none;
    padding: 0;

    li {
        position: relative;
        padding-left: 1.2em;
        margin-bottom: .4em;

        &:before {
            @extend %icon;
            content: $icon-arrow_right;
            position: absolute;
            top: .3em;
            left: 0;
        }
    }
}

