
.alignfull {
    margin-right: calc(50% - 50vw);
    margin-left: calc(50% - 50vw);
    max-width: 100vw;
    width: 100vw;

    &.has-background {
        padding-left: 0;
        padding-right: 0;
    }

}

.alignwide {
    margin-right: calc(25% - 25vw);
    margin-left: calc(25% - 25vw);
}


.has-background {
    a:not(.btn):not(.btn-primary):not(.is-style-btn-primary):not(.btn-secondary):not(.is-style-btn-secondary) {
        color: inherit;

        &:hover {
            text-decoration: none;
        }
    }
    h1, h2, h3, h4, h5, h6 {
        color: inherit;
    }
}

.wp-block-columns {
    &.is-style-reverse {
        flex-wrap: wrap-reverse;
    }

    .wp-block-column {
        //margin-bottom: 1.5em;
        > *:first-of-type{
            margin-top: 0;
        }
    }

}