.block-header-slider {

    .title {
        display: block;
        font-size: 1rem;
        line-height: 1;
        padding-bottom: 1rem;
        @media (min-width: $lg) {
            font-size: 1.3rem;
        }
    }

    h1, h2, h3 {
        margin: 0;
        padding: 0;

        &:after {
            display: none;
        }
    }

    .headline {
        font-weight: normal;
        font-size: 5vw;
        line-height: 1.1;
        text-shadow: 0 0 1px $base-bg-color;
        @media (min-width: $md) {
            font-size: 50px;
        }
    }

    span.headline {
        display: block;
    }

    img {
        display: block;
        width: 100%;
    }

    .btn-primary {
        margin-top: 1.5em;
    }

    .wrapper {
        position: relative;

        .inner {
            position: absolute;
            padding: 1.5em;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

}