.follow {
    text-align: center;
    padding: 2em;

    .links {
        display: inline-block;

        a {
            font-size: 1.5em;
            display: inline-block;
            margin: .2em;
            text-decoration: none;
        }
    }
}

#page-footer {
    padding: 1.5em 0 0;
    background-color: $widgets-bg-color;
    color: $widgets-color;
    border-top: 4px solid $base-bg-color;

    @media (max-width: ($xs - 1px)) {
        text-align: center;
    }

    .wrapper {
        padding-right: 5%;
        padding-left: 5%;

        @media (min-width: $xs) {
            display: flex;
            flex-wrap: wrap;
        }

        > * {
            padding-top: 1.5em;
            @media (min-width: $xs) {
                flex: 1 50%;
                padding: 1rem;
                &.info {
                    flex: 1 100%;
                }
            }
            @media (min-width: $sm) {
                flex: 1 33%;
                &.info {
                    flex: 1 33%;
                }
            }
        }

        .nav-menu {
            li {
                &:before {
                    @extend %icon;
                    content: $icon-arrow_right;
                }
            }
        }
    }

    .title {
        display: block;
        font-weight: 400;
        color: $primary-color;
        margin-bottom: 1em;
    }

    .company {
        font-weight: bold;
    }

    .contact span {
        display: block;
    }

    a {
        color: inherit;
        text-decoration: none;
        display: inline-block;

        &:hover {
            opacity: .9;
        }
    }

    .current-menu-item > a {
        color: $primary-color;
        text-decoration: underline;
    }

    .site-info {
        margin-top: 2em;
        padding: 1em 5%;
        background-color: $footer-bg-color;
        color: $footer-color;
        text-align: center;
        display: flex;
        align-items: center;
        flex-direction: column;
        @media (min-width: $xs) {
            flex-direction: row;
            justify-content: space-between;
        }
        line-height: 1.5;

        .copyright,
        .creator {
            display: inline-block;
        }

        .creator {
            display: flex;
            align-items: center;

            small {
                font-size: 50%;
            }
        }
    }
}