.container,
.alignfull:not(.block-header-slider):not(.wp-block-image):not(.wp-block-cover)  > *,
.alignwide > *:not(.container-large):not(.wp-block-image):not(.wp-block-cover)  {
    padding-right: $container-padding-mobile;
    padding-left: $container-padding-mobile;
    max-width: $container-width-mobile;
    margin-right: auto;
    margin-left: auto;

    @media (min-width: $sm) {
        padding-right: $container-padding-desktop;
        padding-left: $container-padding-desktop;
        max-width: $container-width-desktop;
    }
}

.container-small  {
    @media (min-width: $sm) {
        padding-right: 10%;
        padding-left: 10%;
    }
}

.container-large {
    padding-right: $container-padding-mobile;
    padding-left: $container-padding-mobile;
    @media (min-width: $sm) {
        padding-right: $container-padding-desktop;
        padding-left: $container-padding-desktop;
    }
}

.noscroll {
    overflow: hidden;
}

#content {
    min-height: 60vh;
    padding-bottom: 3rem;

    > *:first-child:not(.wp-block-cover.alignfull):not(.wp-block-image.alignfull){
        margin-top: 3rem;
    }
}