.post-item {
    width: 100%;
    max-width: 450px;
    padding: .7rem;

    @media (min-width: $xs) {
        width: 50%;
    }
    @media (min-width: $sm) {
        width: calc(100% / 3);
    }

    .post-title {
        margin: 0 0 1rem;
        color: $widgets-color;
    }

    .post-content {
        padding: .5em;
        background-color: $widgets-bg-color;
        text-align: center;
        transition: background-color 300ms ease;
        @media (min-width: $sm) and (max-width: ($md - 1px)) {
            font-size: 75%;
        }
    }

}


.post-grid {
    margin: 0 -.7rem;
    padding: 0;
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    @media (min-width: $xs) {
        justify-content: flex-start;
    }
}

.similar-post {
    padding-top: 3em;
    padding-bottom: 3em;
    background-color: $widgets-bg-color;
    color: $widgets-color;

    .post-grid {
        .post-item:nth-child(3) {
            display: none;
            visibility: hidden;
            @media (min-width: $sm) {
                display: block;
                visibility: visible;
            }
        }
    }
}