%button {
    display: inline-block;
    text-decoration: none;
    padding: 0 1em;
    min-height: 40px;
    line-height: 38px;
    transition: all 400ms ease-out;
    font-size: 1rem;
    font-weight: 600;
    text-align: center;
    cursor: pointer;
    border: 1px solid;
    border-radius: 0;
    white-space: nowrap;

    &:hover {
        text-decoration: none;
    }

    &:focus {
        //outline: none;
    }

    &.btn-small {
        line-height: 2rem;
        padding: 0 .7em;
        min-height: 2rem;
        text-transform: none;
    }

    &:after{
        @extend %icon;
        content: $icon-arrow_right;
    }
}

%button-primary {
    @extend %button;
    color: $base-bg-color;
    background: $primary-color;
    border-color: $primary-color;

    &:hover {
        color: $base-bg-color;
        //background-color: darken($primary-color, 15%);
    }

    &:active {
        border-color: $base-color;
    }
}

%button-secondary {
    @extend %button;
    color: $base-color;
    background: $base-bg-color;
    border-color: $secondary-color;

    &:hover {
        //color: $base-bg-color;
        //background-color: darken($base-bg-color, 20%);
    }

    &:active {
        border-color: $primary-color;
    }
}


.btn,
.btn-primary,
input[type="submit"],
.wp-block-button.is-style-btn-primary .wp-block-button__link {
    @extend %button-primary;
}

.btn-secondary,
.wp-block-button.is-style-btn-secondary .wp-block-button__link {
    @extend %button-secondary;
}