.block-team {
    &:not(.persons-1) {
        display: flex;
        flex-wrap: wrap;
        margin-right: -.5rem;
        margin-left: -.5rem;
        @media (min-width: $sm) {
            margin-right: -1.5rem;
            margin-left: -1.5rem;
        }

        .item {
            padding: .5rem;
            width: 50%;
            @media (min-width: $sm) {
                padding: 1.5rem;
            }
            @media (min-width: $md) {
                width: calc(100% / 3);
            }
            @media (min-width: $xl) {
                width: 25%;
            }
        }
    }

    .name,
    .position {
        display: block;
    }

}