#navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 900;
    height: $nav-height-mobile;
    background-color: $nav-bg-color;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 5%;
    padding-left: 5%;

    @media (min-width: $breakpoint-nav) {
        height: $nav-height-desktop;
    }

    .brand {
        margin-right: 3em;

        a {
            display: block;
        }

        img {
            height: $nav-height-mobile;
            width: auto;
            max-width: none;
            @media (min-width: $breakpoint-nav) {
                height: $nav-height-desktop;
            }
        }
    }
}

$navMargin: (($nav-height-mobile / 2) - 2px);
#menu-button {
    display: block;
    height: $nav-height-mobile;
    cursor: pointer;

    @media (min-width: $breakpoint-nav) {
        display: none;
    }

    .sign {
        display: block;
        height: 2px;
        width: 36px;
        margin: $navMargin 0 $navMargin 20px;
        border-radius: 2px;
        background-color: $nav-color;
        position: relative;
        transition: background-color 200ms linear, transform 300ms linear;

        &:before,
        &:after {
            content: "";
            display: block;
            height: 2px;
            width: 30px;
            border-radius: 2px;
            background-color: $nav-color;
            position: absolute;
            right: 0;
            transition: transform 300ms linear;
        }

        &:before {
            transform: translateY(-10px);
        }

        &:after {
            transform: translateY(10px);
        }
    }
}

.menu-toggle {
    position: absolute;
    right: 0;
    top: 1px;
    padding: 25px 11px 24px;
    cursor: pointer;
    background-color: rgba($nav-color, 0);
    transition: background-color 250ms linear;

    @media (min-width: $breakpoint-nav) {
        display: none;
    }

    em {
        display: block;
        position: relative;
        width: 28px;
        height: 0;

        &:before,
        &:after {
            content: "";
            display: block;
            height: 1px;
            width: 15px;
            position: absolute;
            transition: transform 200ms linear;
            background-color: $nav-color;
        }

        &::before {
            left: 0;
            transform: rotate(45deg) translateY(-2px);
        }

        &:after {
            right: 0;
            transform: rotate(-45deg) translateY(-2px);
        }
    }
}

.nav-menu,
.sub-menu {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

#primary-nav {
    position: fixed;
    top: $nav-height-mobile;
    right: 0;
    left: 0;
    max-height: calc(95vh - #{$nav-height-mobile});
    overflow-y: auto;
    background-color: $nav-bg-color;
    opacity: 0;
    pointer-events: none;
    box-shadow: 0 2px 3px rgba($base-color, .2);

    @media (min-width: $breakpoint-nav) {
        position: static;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex: 1;
        height: ($nav-height-desktop - 20px);
        margin-top: 20px;
        overflow: visible;
        opacity: 1;
        pointer-events: auto;
        background-color: transparent;
        box-shadow: none;
    }

    .nav-area {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 1em $container-padding-mobile;
        background-color: $nav-bg-color;

        > * {
            @media (min-width: $breakpoint-nav) {
                margin-left: 1.2em;
            }
        }

        a, label {
            color: $nav-color;
            text-decoration: none;
            cursor: pointer;

            &:hover {
                opacity: .8;
            }
        }

        .languages {
            list-style-type: none;
            margin: 0;
            padding: 0;

            li {
                display: inline-block;

                &:after {
                    content: " /";
                }

                &:last-child:after {
                    display: none;
                }

                &.current-lang a {
                    text-decoration: underline;
                }
            }
        }

        .login {

        }

        .inquiry {
            font-size: 1.4em;
        }
    }

    .nav-menu {
        padding: 1em $container-padding-mobile;

        @media (min-width: $sm) {
            padding: 2em $container-padding-desktop;
        }

        @media (min-width: $breakpoint-nav) {
            display: inline-block;
            padding: 0;
        }

        a {
            display: block;
            text-decoration: none;
        }

        li {
            position: relative;
            @media (min-width: $breakpoint-nav) {
                white-space: nowrap;
            }
        }

        > li {
            border-bottom: 1px solid $nav-color;

            &:last-child {
                border: none;
            }

            @media (min-width: $breakpoint-nav) {
                display: inline-block;
                border: none;
                background-color: transparent;
                margin-right: 1em;

                &:hover .sub-menu {
                    opacity: 1;
                    pointer-events: auto;
                }

            }

            &.current-menu-item > a {
                color: $nav-active-color;
                @media (min-width: $breakpoint-nav) {
                    box-shadow: inset 0 -2px 0 $nav-active-color;
                }
            }

            &.current-menu-parent > a {
                @media (min-width: $breakpoint-nav) {
                    color: $nav-active-color;
                }
            }

            > a {
                height: 50px;
                line-height: 50px;
                font-size: 1.3em;
                color: $nav-color;

                &:hover {
                    color: $nav-active-color;
                }

                @media (min-width: $breakpoint-nav) {
                    height: 40px;
                    line-height: 40px;
                    font-size: 1em;
                    //color: $primary-color;


                }

            }

            > .sub-menu {
                //background-color: darken($primary-color, 2%);
                display: none;
                padding-bottom: 1em;
                background-color: $subnav-bg-color;

                @media (min-width: $breakpoint-nav) {
                    //background-color: $primary-color;
                    display: block;
                    min-width: 180px;
                    padding: .5rem;
                    position: absolute;
                    top: 100%;
                    left: -.5rem;
                    z-index: 1;
                    opacity: 0;
                    pointer-events: none;
                }

                li {
                    height: 0;
                    transform: scaleY(0);

                    @media (min-width: $breakpoint-nav) {
                        transform: none;
                        height: auto;
                    }

                    &.current-menu-item > a {
                        color: $subnav-active-color;
                    }
                }

                a {
                    //font-size: 1.1em;
                    line-height: 1.2;
                    padding: .5em 0 .5em .7em;
                    color: $subnav-color;

                    &:hover {
                        color: $subnav-active-color;
                    }

                    @media (min-width: $breakpoint-nav) {
                        //font-size: 1em;
                        padding: .4em 1rem;
                    }
                }

            }

        }

    }

    input[type="checkbox"] {
        display: none;

        &:checked {
            ~ .menu-toggle {
                //background-color: darken($primary-color, 2%);
                em:before,
                em:after {
                    transform: rotate(0) translateY(-2px);
                }
            }

            ~ .sub-menu {
                display: block;

                li {
                    @media (max-width: #{$breakpoint-nav - 1px}) {
                        height: auto;
                        transform: scaleY(1);
                    }
                }
            }
        }
    }
}

.js {
    #primary-nav {
        transition: opacity 200ms linear;
        .nav-menu {
            > li {
                > .sub-menu {
                    @media (min-width: $breakpoint-nav) {
                        transition: opacity 200ms;
                    }

                    li {
                        transition: transform 200ms linear, height 200ms linear;

                    }
                }
            }
        }
    }
}

#trigger-menu {
    display: none;

    &:checked {
        ~ #primary-nav {
            opacity: 1;
            pointer-events: auto;
        }

        ~ #menu-button {
            .sign {
                background-color: rgba($nav-color, 0);
                transform: translateX(-30%);

                &:before {
                    transform: rotate(-45deg);
                }

                &:after {
                    transform: rotate(45deg);
                }
            }
        }
    }
}


.admin-bar {
    #navbar {
        //top: 46px;
        @media (min-width: 783px) {
            top: 32px;
        }
    }

    #primary-nav {
        //top: $nav-height-mobile + 46px;
        @media (min-width: 783px) {
            top: $nav-height-mobile + 32px;
        }

    }
}
